<template>
  <div>
    <van-sticky>
      <van-row style="padding: 10px; text-align: center;">
        <van-col span="8">
          <van-button icon="clock" :type="buttonToday" round size="small" @click="clickToday">今天 {{ today }}</van-button>
        </van-col>
        <van-col span="8">
          <van-button icon="clock" :type="buttonTomorrow" round size="small" @click="clickTomorrow">明天 {{ tomorrow }}</van-button>
        </van-col>
        <van-col span="8">
          <van-button icon="clock" :type="buttonRecently" round size="small" @click="clickRecently">最近 {{ recently }}</van-button>
        </van-col>
      </van-row>
    </van-sticky>
    <div>
      <van-pull-refresh v-model="refreshing" :style="`height: ${refreshHeight}px;overflow:auto;`" @refresh="onRefresh()">
        <van-empty v-if="list.length === 0" image="search" description="暂无数据" />
        <template v-else>
          <div v-for="(item, key) in list" :key="key">
            <clean-card v-if="item.data_type === 'clean'" :item="item" />
            <qc-card v-if="item.data_type === 'qc'" :item="item" />
            <transport-card v-if="item.data_type === 'transport'" :item="item" @refreshList="getDataList" />
            <task-card v-if="item.data_type === 'task'" :item="item" />
          </div>
        </template>
        <van-calendar v-model="showCalendar" :show-confirm="false" :min-date="minDate" :max-date="maxDate" @confirm="onConfirm" />
      </van-pull-refresh>
    </div>
  </div>
</template>

<script>
import QcCard from '@/views/ticket/qc/index.vue'
import CleanCard from '@/views/ticket/clean/index.vue'
import TransportCard from '@/views/ticket/transport/index.vue'
import TaskCard from '@/views/ticket/task/index.vue'

export default {
  name: 'TicketIndex',
  components: {TaskCard, TransportCard, CleanCard, QcCard},
  filters: {
    statusFilter(val) {
      const map = {
        0: '未开始',
        1: '进行中',
        10: '已完成',
        20: '已过期'
      }
      return map[val]
    },
    dataTypeFilter(val) {
      const map = {
        'clean': '保洁',
        'qc': '巡更',
        'transport': '运送',
        'task': '任务'
      }
      return map[val]
    }
  },
  data() {
    return {
      today: this.$date().format('MM-DD'), // 今日日期
      tomorrow: this.$date().add(1, 'days').format('MM-DD'), // 明日日期
      recently: '', // 最近日期
      list: [],
      buttonToday: 'default',
      buttonTomorrow: 'default',
      buttonRecently: 'default',
      showCalendar: false,
      minDate: this.$date().subtract(1, 'month').toDate(),
      maxDate: this.$date().add(1, 'month').toDate(),
      refreshing: false,
      refreshHeight: window.innerHeight - 52,
      ticketDate: this.$date().format('YYYY-MM-DD')
    }
  },
  mounted() {
    this.buttonToday = 'info'
    this.getDataList()
  },
  methods: {
    statusChange(status) {
      const map = { 0: 'default', 1: 'success', 10: 'primary', 20: 'danger'}
      return map[status]
    },
    async getDataList() {
      const res = await this.$post('/ticket/list', { ticket_date: this.ticketDate })
      if (res && res.length > 0) {
        this.list = res
      } else {
        this.list = []
      }
    },
    clickToday() {
      this.buttonToday = 'info'
      this.buttonTomorrow = 'default'
      this.buttonRecently = 'default'
      this.ticketDate = this.$date().format('YYYY-MM-DD')
      this.getDataList()
    },
    clickTomorrow() {
      this.buttonToday = 'default'
      this.buttonTomorrow = 'info'
      this.buttonRecently = 'default'
      this.ticketDate = this.$date().add(1, 'days').format('YYYY-MM-DD')
      this.getDataList()
    },
    clickRecently() {
      this.buttonToday = 'default'
      this.buttonTomorrow = 'default'
      this.buttonRecently = 'info'
      this.showCalendar = true
    },
    onConfirm(date) {
      this.showCalendar = false
      this.recently = this.$date(date).format('MM-DD')
      this.ticketDate = this.$date(date).format('YYYY-MM-DD')
      this.getDataList()
    },
    onRefresh() {
      setTimeout(() => {
        this.refreshing = false
        this.getDataList()
      }, 1000)
    }
  }
}
</script>

